import handleError from '../utils/handleError'
import { Consys_Axios } from '../index'
import { Check_Access } from './Utils'
import { Consys_Config } from '../../Config'

import { ConsysRefs, DataAvgType } from '../utils/defines'

const FEED_ID = ConsysRefs.VALVEFEED //100

async function fetch({sysId=1, start=null, stop=0}, _rend) {
  var Feed_Data = []
  var state = _rend.getState().consys
  if (state && state.feed) {
    Feed_Data = {...state.feed}
  }

  var DataCall = []
  if (Feed_Data[sysId]) {
    var Sys_Feed = Feed_Data[sysId]
    if (Sys_Feed.oldest && Sys_Feed.oldest.timestamp) {
      if (Sys_Feed.oldest.timestamp*1000 > stop) {
        DataCall.push({start:Sys_Feed.oldest.timestamp, stop:stop/1000})
      }
    }
    if (Sys_Feed.lastId && Sys_Feed.lastId.timestamp) {
      if (!start || Sys_Feed.lastId.timestamp*1000 < start) {
        DataCall.push({start:null, stop:Sys_Feed.lastId.timestamp})
      }
    }
  } else {
    Feed_Data[sysId] = {data:[]}
    DataCall.push({start:start, stop:stop/1000})
  }

  if (DataCall.length > 0) {
    var Access = await Check_Access(_rend.getState().firebase)
    if (Access && Access.SiteUrl) {
      var limit = 3000
      var res = await Promise.all(DataCall.map(async FetchData => {
        var offset = 0
        var lastId = null

        var DataDone = true
        do
        {
          DataDone = true
          var baseURL = Consys_Config.GetConsumtionData_URL(Access.SiteUrl, FEED_ID, sysId, offset, limit, FetchData.start, FetchData.stop, lastId)
          //console.log("fetch", baseURL)

          var consumptiondata = await Consys_Axios(baseURL).get().then(async (response)=> {
            if (!response || !response.data) {
              console.log('Consys Feed get data Error')
              return handleError({ message: 'Consys Feed get data Error' })
            }
            var ResData = response.data
            //console.log(ResData)
            if (!ResData.result === "OK"){
              console.log('Consys Feed data result Error')
              return handleError({ message: 'Consys Feed data result Error' })
            }
            if (ResData.consumptiondata) {
              var consumptiondata = ResData.consumptiondata
              if (consumptiondata) {
                var oldest = Feed_Data[sysId].oldest
                var lastId = Feed_Data[sysId].oldest
                var data = {...Feed_Data[sysId].data}
    
                consumptiondata.forEach(element => {
                  var weight = element.amount /1000
    
                  var Ventildata = data[element.valve]?{...data[element.valve]}:{}
                  Ventildata[element.dataid] = {
                    unit: element.unit,
                    unitname: element.unit,
                    silono: element.silono,
                    siloname: element.silono,
                    funit: 0,
                    cost: 0,
                    amount: weight,
                    timestamp: (element.timestamp)
                  }
    
                  if (!oldest || oldest.timestamp > element.timestamp) oldest = {timestamp: element.timestamp, dataid:element.dataid, valve:element.valve}
                  if (!lastId || lastId.timestamp < element.timestamp) lastId = {timestamp: element.timestamp, dataid:element.dataid, valve:element.valve}
    
                  data[element.valve] = Ventildata
                })
                
                Feed_Data[sysId] = {data:data, oldest:oldest, lastId:lastId}
                if (consumptiondata.length === 3000) {
                  offset += 3000
                  DataDone = false
                }
              }
              return ResData.consumptiondata
            }else return null
          }).catch((error) => {
            console.log("Get Feed error", error)
          })

          //console.log(consumptiondata) // todo: if check if error not res
          
        } while (!DataDone)
      }));
    } else return handleError({ message: 'Consys Feed Access Error' })
  }

  state = _rend.getState().consys
  if (state && state.feed) {
    Feed_Data = {...state.feed, ...Feed_Data}
  }
  //console.log(Feed_Data)
  return Feed_Data
}

export {
  fetch
}