import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
} from 'react-native'
import { LoadingSplash, Brand, Frame, Button } from '../../../Components'
import { SectionGrid } from 'react-native-super-grid';

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { EditData, ErrorHandler, SetData } from '../../../Store/Firebase/Utils'
import FirebaseSection from '../../../Store/Firebase/Section'
import FirebasePen from '../../../Store/Firebase/Pen'
import FirebaseWeighing from '../../../Store/Firebase/Weighing'
import FirebaseBatch from '../../../Store/Firebase/Batch'
import FirebaseGraph from '../../../Store/Firebase/Graph'
import FirebaseAutopig from '../../../Store/Firebase/Autopig'

import ConsysScale from '../../../Store/Consys/Scale'
import ConsysFeed from '../../../Store/Consys/Feed'

import { WeightCalc } from '../../../Services/Firebase/Utils'

import { DataRefs, DataAvgType, ConsysRefs } from '../../../Services/utils/defines'
import { ObjectsEqual, sortObject } from '../../../Services/utils/formators'

const Pens = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images, Colors } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const farmId = useSelector(state => state.firebase.activeData&&state.firebase.activeData.farm?state.firebase.activeData.farm:undefined)
  const sectionDatas = useSelector(state => state.firebase.sections)
  const pensDatas = useSelector(state => state.firebase.pens)

  const batchDatas = useSelector(state => state.firebase.batchs)
  const graphDatas = useSelector(state => state.firebase.graphs)
  const weighingsDatas = useSelector(state => state.firebase.weighings)
  const autoPigs = useSelector(state => state.firebase.autopigs)
  const consysData = useSelector(state => state.consys)

  const [PenData, setPenData] = useState([])

  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
    return () => {
      // clean up
    };
  }, [])

  useEffect(async () => { //Error if farmid not equal ???... multiple
    if (farmId) {
      dispatch(FirebaseSection.action({FarmId: farmId}))
      dispatch(FirebaseBatch.action({FarmId: farmId}))
      dispatch(FirebaseGraph.action({FarmId: farmId}))
      dispatch(FirebasePen.action({})).then(data => {
        if (data.meta.requestStatus === "fulfilled") {
          dispatch(FirebaseWeighing.action({FarmId: farmId, PenId: null, SectionId: null, DataTime:weighingsDatas&&weighingsDatas.update_time?weighingsDatas.update_time:null}))
          dispatch(FirebaseAutopig.action({FarmId: farmId}))
        }
      })
    }

    return () => {
      
    }
  },[farmId])

  
  useEffect(() => { //Weight DataCalc
    var isCancelled = false;
    const WeightDataCalc = async (isCancelled) => {
      if(pensDatas) {
        //await Promise.all(Object.entries(pensDatas).map(async ([Id,Pen])=>{
        Object.entries(pensDatas).map(async ([Id,Pen])=>{
          if (Id === "update_time") return;
          //var color = Colors.gray
          //var displayWeight = null

          if (!isCancelled && Pen.datas && Pen.datas.batch && batchDatas && batchDatas[Pen.datas.batch]) {
            var batch = batchDatas[Pen.datas.batch]
            if (batch && batch.gcurve && graphDatas && graphDatas[batch.gcurve]) {
              var Curve = graphDatas[batch.gcurve]
              if (!isCancelled && Curve && weighingsDatas && weighingsDatas[Id]) {
                //could be await ...
                WeightCalc({ Weighings:weighingsDatas[Id], Batch:batch, Graphs:Curve}).then( (data) => {
                  var color = Colors.gray
                  var displayWeight = data.PenWeight
                  displayWeight = (Math.round(displayWeight * 100) / 100)

                  var TargetWeight = data.Batch.datas.target?data.Batch.datas.target:null
                  if (TargetWeight && displayWeight >= TargetWeight) {
                    color = Colors.pigpink
                  } else if (data.isVirtual) {
                    var devianceMin = data.Batch.datas.devianceMin?data.Batch.datas.devianceMin:3
                    var devianceMax = data.Batch.datas.devianceMax?data.Batch.datas.devianceMax:3
                    var Weight = data.isVirtual.Now
                    var ExValue = data.isVirtual.TempEx
    
                    var devianceValMin = (ExValue/100)* devianceMin
                    var devianceValMax = (ExValue/100)* devianceMax
                    
                    if (ExValue && Weight < ExValue && Weight < ExValue - devianceValMin) {
                      color = Colors.error
                    } else if (ExValue && Weight > ExValue && Weight > ExValue + devianceValMax) {
                      color = Colors.overest
                    } else {
                      color = Colors.success
                    }
                  }
                  if (!isCancelled) {
                    setPenData(data => {
                      var NewData = {[Id]:{}}
                      if (data[Id]) NewData[Id] = {...data[Id]}
                      if (Pen.datas && !NewData[Id].datas) NewData[Id].datas = Pen.datas
                      NewData[Id].color = color
                      NewData[Id].displayWeight = displayWeight
                      if (NewData[Id] && (!data[Id] || !ObjectsEqual(NewData[Id] && data[Id]))){ 
                        var State = {
                          ...data,
                          ...NewData
                        }
                        return State
                      }
                    })
                  }
                })
              }
            }
          }
        })
        //)
      }
    }
    WeightDataCalc(isCancelled)
    return () => {
      // clean up
      isCancelled = true
    };
  },[weighingsDatas])

  useEffect(() => { //autoPigs DataCalc
    var isCancelled = false;
    const autoPigsDataCalc = async (isCancelled) => {
      if(pensDatas && !isCancelled) {
        var CalledCon = {scale:[],feed:[]}
        await Promise.all(Object.entries(pensDatas).map(async ([Id,Pen])=> {
          if (!isCancelled && Pen.datas && Pen.datas.autopigs) {
            var AutoPig = Pen.datas.autopigs;
            var AutoFrame = false
            await Promise.all(AutoPig.split('').map(autoid => {
              if (autoPigs && autoPigs[autoid] && autoPigs[autoid][Id]) {
                var AutoPigData = autoPigs[autoid][Id]
                //console.log("Pen: " + Id + " is autopig property: " + autoid)
              
                if (AutoPigData.scale && !isCancelled) {
                  AutoFrame = true
                  if (!CalledCon.scale[autoid]) {
                    var STime = (Date.now() - (86400000*2));
                    dispatch(ConsysScale.action({sysId:autoid, stop:STime}))
                    CalledCon.scale[autoid] = true
                  }
                }
                if (AutoPigData.feed && !isCancelled) {
                  if (!CalledCon.feed[autoid]) {
                    var STime = (Date.now() - (86400000*2));
                    dispatch(ConsysFeed.action({sysId:autoid, stop:STime}))
                    CalledCon.feed[autoid] = true
                  }
                }
              }
            })).then(() => {
              if (!isCancelled) {
                setPenData(data => {
                  var NewData = {[Id]:{}}
                  if (data[Id]) NewData[Id] = {...data[Id]}
                  if (Pen.datas && !NewData[Id].datas) NewData[Id].datas = Pen.datas
                  NewData[Id].AutoPig = AutoPig
                  NewData[Id].AutoFrame = AutoFrame
                  if (NewData[Id] && (!data[Id] || !ObjectsEqual(NewData[Id] && data[Id]))){ 
                    var State = {
                      ...data,
                      ...NewData
                    }
                    return State
                  }
                })
              }
            })
          }
        }))
      }
    }
    autoPigsDataCalc(isCancelled)
    return () => {
      // clean up
      //isCancelled = true
    };
  },[autoPigs])

  useEffect(() => { //consys DataCalc
    var isCancelled = false;
    const consysDataCalc = async (isCancelled) => {
      if (consysData && autoPigs && PenData) {
        var PenDATAS = {...PenData}
        await Promise.all(Object.entries(pensDatas).map(async ([Id,Pen])=> {
          if (!isCancelled && Pen.datas && Pen.datas.autopigs) {
            var active = Pen.datas.count&&Pen.datas.count>0?true:false

            var warn = false

            Pen.datas.autopigs.split('').every(autoid => {
              if (isCancelled || warn) return false;
              
              if (!active && !warn) {
                if (autoPigs[autoid] && autoPigs[autoid][Id]) {
                  if (autoPigs[autoid][Id].scale && consysData.scale && consysData.scale[autoid] /* && something today */) {
                    warn = true
                  }
                  else if (autoPigs[autoid][Id].activity && consysData.activity && consysData.activity[autoid] /* && something today */) warn = true
                  else if (autoPigs[autoid][Id].temp && consysData.temperature && consysData.temperature[autoid] /* && something today */) warn = true
                  else if (autoPigs[autoid][Id].water && consysData.water && consysData.water[autoid] /* && something today */) warn = true
                  else if (autoPigs[autoid][Id].feed && consysData.feed && consysData.feed[autoid] /* && something today */) {
                    var valve_Id = autoPigs[autoid][Id].consysId?autoPigs[autoid][Id].consysId:Id
                    var ConsysId = valve_Id.trim()
                    if (isNaN(ConsysId)) {
                      var PreIds = []
                      var PostIds = []
                      if (autoPigs["Sys_Config"]) {
                        if (autoPigs["Sys_Config"].PrePen) PreIds = autoPigs["Sys_Config"].PrePen
                        if (autoPigs["Sys_Config"].PostPen) PostIds = autoPigs["Sys_Config"].PostPen
                      } 
                      PreIds.forEach(element => {
                        if (ConsysId.startsWith(element)) ConsysId = ConsysId.slice(element.length, ConsysId.length)
                      });
                      if (isNaN(ConsysId)){
                        PostIds.forEach(element => {
                          if (ConsysId.startsWith(element)) ConsysId = ConsysId.replace(element,'');
                        });
                      }
                    }
                    if (consysData.feed[autoid][ConsysId] /* && something today */) warn = true
                  }
                  else if (autoPigs[autoid][Id].teweid && consysData.tewefeed && consysData.tewefeed[autoid] /* && something today */) warn = true
                }
              }
              return true
            });
            if (warn && !isCancelled) {
              if (PenDATAS[Id]) PenDATAS[Id].color = Colors.BUMBLEBEE;
              else PenDATAS[Id] = { color: Colors.BUMBLEBEE }
            }
          }
        }))

        setPenData(data => {
          return {...data, ...PenDATAS}
        })
      }
    }
    consysDataCalc(isCancelled)
    return () => {
      // clean up
      //isCancelled = true
    };
  }, [consysData, autoPigs])

  if (sectionDatas && typeof sectionDatas !== 'undefined') {
    return (
      <SafeAreaView style={[Layout.fill, Layout.fullSize, Layout.colVCenter, Common.backgroundPrimary]}>
        <SectionGrid style={[Layout.fill, Layout.fullSize, Gutters.smallVPadding, Gutters.smallHPadding]}
          itemDimension={75}
          itemContainerStyle={[{width:75,height:75}]}
          spacing={2}
          sections={
            Object.keys(sectionDatas).filter(e => e !== 'update_time').map((Section) => {
              var Pens = sortObject(sectionDatas[Section].pens)
              return({
                title: Section,
                data: Object.keys(Pens)
              })
            })
          }

          renderSectionHeader={({ section }) => (
            <View style={[Layout.midWidth, {marginBottom:0}, isApp()?{width:"65%"}:{width:"100%"}, ]}>
              <Button Texts={[section.title]} Title={true} />
            </View>
          )}

          renderItem={(item) => {
            var color = Colors.gray
            var displayWeight = null
            var AutoFrame = false;

            if (PenData[item.item]){
              var Pen = PenData[item.item]
              if (Pen.color) color = Pen.color
              if (Pen.displayWeight) displayWeight = Pen.displayWeight
              if (Pen.AutoFrame) AutoFrame = Pen.AutoFrame
            }
            return(
              <TouchableOpacity style={[Common.button.outlineRounded, {borderColor:'#000000', borderWidth:AutoFrame?5:0, width:70, height:70, alignItems:"center", justifyContent:"center", backgroundColor:color}]}
                onPress={() => {
                  //pen = item.item  || item.section.originalData[item.index]
                  //section = item.section.title 
                  var batch = Pen && Pen.datas && Pen.datas.batch?Pen.datas.batch:null
                  dispatch(EditData.action({DataRef:DataRefs.ACTIVE, Data:{farm:farmId, section:item.section.title, pen:item.item, batch:batch}}))
                  navigationRef.current?.navigate('User', {screen: 'Pen_Display'})
                }}>
                <Text>{item.item}</Text>
                {displayWeight?(<Text>{displayWeight + " kg"}</Text>):(<></>)}
              </TouchableOpacity>
            )
          }}

          />
      </SafeAreaView>
    )
  } else {
    return (<LoadingSplash />) 
  }
}

export default Pens
