import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  View,
  Text,
  TouchableOpacity,
  SafeAreaView,
  ScrollView,
  FlatList
} from 'react-native'
import { LoadingSplash, Brand, Frame, Button, ContentProp, CurveViewer, OperationsPanel } from '../../../Components'
import { SectionGrid } from 'react-native-super-grid';

import { useTheme } from '../../../Theme'
import { useTranslation } from 'react-i18next'
import { navigationRef, isApp } from '../../../Navigators/Root'

import { ErrorHandler, SetData, WeightCalculate } from '../../../Store/Firebase/Utils'

import FirebaseWeighing from '../../../Store/Firebase/Weighing'
import FirebaseComment from '../../../Store/Firebase/Comment'
import FirebaseBatch from '../../../Store/Firebase/Batch'
import FirebaseGraph from '../../../Store/Firebase/Graph'
import FirebaseAutopig from '../../../Store/Firebase/Autopig'

import ConsysScale from '../../../Store/Consys/Scale'
import ConsysFeed from '../../../Store/Consys/Feed'

import { DataRefs, DataStates, Operations, DataAvgType } from '../../../Services/utils/defines'
import { ObjectsEqual, sortObject } from '../../../Services/utils/formators'

const Pen_Display = () => {
  const { t } = useTranslation()
  const { Common, Fonts, Gutters, Layout, Images, Colors } = useTheme()
  const dispatch = useDispatch()

  const firebaseLoading = useSelector(state => state.firebase.loading)
  const firebaseError = useSelector(state => state.firebase.error)

  const activeData = useSelector(state => state.firebase.activeData?state.firebase.activeData:undefined)
  const batchDatas = useSelector(state => activeData&&activeData.batch&&state.firebase.batchs&&state.firebase.batchs[activeData.batch]?state.firebase.batchs[activeData.batch]:undefined)
  const graphDatas = useSelector(state => batchDatas&&batchDatas.gcurve&&state.firebase.graphs&&state.firebase.graphs[batchDatas.gcurve]?state.firebase.graphs[batchDatas.gcurve]:undefined)

  const penDatas = useSelector(state => activeData&&activeData.pen&&state.firebase.pens&&state.firebase.pens[activeData.pen]?state.firebase.pens[activeData.pen]:undefined)
  const weighingsDatas = useSelector(state => activeData&&activeData.pen&&state.firebase.weighings&&state.firebase.weighings[activeData.pen]?state.firebase.weighings[activeData.pen]:undefined)
  const commentsDatas = useSelector(state => activeData&&activeData.pen&&state.firebase.comments&&state.firebase.comments[activeData.pen]?state.firebase.comments[activeData.pen]:undefined)
  
  const autoPigs = useSelector(state => state.firebase.autopigs)
  const consysData = useSelector(state => state.consys)

  const [weightCalcs, setweightCalcs] = useState({})
  const [ConsysScales, setConsysScales] = useState([])
  const [ConsysFeeds, setConsysFeeds] = useState([])

  const [LastFeed, setLastFeed] = useState(null)
  const [FeedList, setFeedList] = useState([])
  const [CombinedFeed, setCombinedFeed] = useState([])

  useEffect(() => {
    if (firebaseError && firebaseError.message) dispatch(ErrorHandler.action())
  },[])

  useEffect(() => { //Error if farmid not equal ???... multiple
    if (activeData && activeData.farm && activeData.pen) {
      dispatch(FirebaseAutopig.action({FarmId: activeData.farm}))
      dispatch(FirebaseWeighing.action({FarmId: activeData.farm, PenId: activeData.pen, SectionId: activeData.section}))
      dispatch(FirebaseComment.action({FarmId: activeData.farm, PenId: activeData.pen}))
      dispatch(FirebaseBatch.action({FarmId: activeData.farm, BatchId: activeData.batch})).then(data => {
        if (data.meta.requestStatus === "fulfilled"){ // secound hand load Gruve from batch data
          var Datas = data.payload
          if (Datas && Datas.gcurve) {
            dispatch(FirebaseGraph.action({FarmId: activeData.farm, GraphId: Datas.gcurve}))
          }
        }
      })
    }
  },[activeData])
  
  const [last_ActiveData, setlast_ActiveData] = useState(null)
  useEffect(() => {
    if (weighingsDatas && graphDatas && !ObjectsEqual(weighingsDatas, last_ActiveData)) {
      setlast_ActiveData(weighingsDatas)
      dispatch(WeightCalculate.action({Weighings: weighingsDatas, Batch:batchDatas, Graphs:graphDatas})).then(data => {
        if (data.meta.requestStatus === "fulfilled") {
          setweightCalcs(data.payload)
        }
      })
    }
  },[weighingsDatas, graphDatas])

  useEffect(() => { //autoPigs DataCalc
    var isCancelled = false;
    const autoPigsDataCalc = async (isCancelled) => {
      if (penDatas && autoPigs && consysData) {
        if (penDatas.datas && penDatas.datas.autopigs) {
          var CalledCon = {scale:[],feed:[]}
          penDatas.datas.autopigs.split('').every(async autoid => {
            if (autoPigs[autoid] && autoPigs[autoid][activeData.pen]) {
              
              if (autoPigs[autoid][activeData.pen].scale) {
                if (!CalledCon.scale[autoid]) {
                  CalledCon.scale[autoid] = true
                  dispatch(ConsysScale.action({sysId:autoid, stop:batchDatas&&batchDatas.time?batchDatas.time:null}))
                }
                if (consysData.scale && consysData.scale[autoid]) {
                  setConsysScales(old => {
                    if (!old.includes(autoid)) {
                      var newScale = [...old]
                      newScale.push(autoid)
                      return newScale
                    }
                    return old
                  })
                }
              }
              if (autoPigs[autoid][activeData.pen].feed) {
                if (!CalledCon.feed[autoid]) {
                  CalledCon.feed[autoid] = true
                  dispatch(ConsysFeed.action({sysId:autoid, stop:batchDatas&&batchDatas.time?batchDatas.time:null}))
                }
              }
            }
          })
        }
      }
    }
    autoPigsDataCalc(isCancelled)
    return () => {
      // clean up
      isCancelled = true
    };
  },[autoPigs])

  useEffect(() => { //autoPigs DataCalc
    var isCancelled = false;
    const consysDataCalc = async (isCancelled) => {
      if (penDatas && autoPigs && consysData) {
        if (penDatas.datas && penDatas.datas.autopigs) {
          penDatas.datas.autopigs.split('').every(async autoid => {
            if (autoPigs[autoid] && autoPigs[autoid][activeData.pen]) {
              if (autoPigs[autoid][activeData.pen].feed) {
                var valve_Id = autoPigs[autoid][activeData.pen].consysId?autoPigs[autoid][activeData.pen].consysId:activeData.pen
                var ConsysId = valve_Id.trim()
                if (isNaN(ConsysId)) {
                  var PreIds = []
                  var PostIds = []
                  if (autoPigs["Sys_Config"]) {
                    if (autoPigs["Sys_Config"].PrePen) PreIds = autoPigs["Sys_Config"].PrePen
                    if (autoPigs["Sys_Config"].PostPen) PostIds = autoPigs["Sys_Config"].PostPen
                  } 
                  PreIds.forEach(element => {
                    if (ConsysId.startsWith(element)) ConsysId = ConsysId.slice(element.length, ConsysId.length)
                  });
                  if (isNaN(ConsysId)){
                    PostIds.forEach(element => {
                      if (ConsysId.startsWith(element)) ConsysId = ConsysId.replace(element,'');
                    });
                  }
                }
                if (consysData.feed && consysData.feed[autoid] && consysData.feed[autoid].data && consysData.feed[autoid].data[ConsysId]) {
                  setConsysFeeds(old => {
                    if (!old.includes(autoid)) {
                      var newFeed = [...old]
                      newFeed.push({sysid:autoid,valve:ConsysId, pen:activeData.pen})
                      return newFeed
                    }
                    return old
                  })
                }
              }
            }
          })
        }
      }
    }
    consysDataCalc(isCancelled)
    return () => {
      // clean up
      isCancelled = true
    };
  },[consysData])

  useEffect(() => { //consys DataCalc
    var isCancelled = false;
    const consysDataCalc = async (isCancelled) => {
      if (ConsysFeeds && ConsysFeeds.length && consysData.feed) {
        var Combined = []
        var DailyLog = []
        ConsysFeeds.forEach(FeedElement => {
          var FeedData = {}
          if (consysData.feed[FeedElement.sysid] && consysData.feed[FeedElement.sysid].data && consysData.feed[FeedElement.sysid].data[FeedElement.valve]) {
            FeedData = consysData.feed[FeedElement.sysid].data[FeedElement.valve]
          }

          Object.entries(FeedData).map(async ([Id,Feed])=> {
            if (FeedElement.valve && activeData.pen) {
              if (!LastFeed || LastFeed.timestamp < Feed.timestamp) setLastFeed(Feed)
              var Stime = batchDatas&&batchDatas.time?batchDatas.time:(Date.now() / 1000) - (86400*2);
              if (Feed.timestamp*1000 >= Stime) {
                var time = new Date(Feed.timestamp * 1000)
                var TimeString = time.getFullYear() + "/" + (time.getMonth() +1 ) + "/" + time.getDate()

                var Unitindex = DailyLog.findIndex(i => {
                  if (i.siloname === Feed.siloname){
                    var time = new Date(i.timestamp)
                    var TimeString2 = time.getFullYear() + "/" + (time.getMonth() +1 ) + "/" + time.getDate()
                    if (TimeString === TimeString2) {
                      return true
                    }
                    return false
                  }
                })

                var UnitItem = {
                  timestamp: Feed.timestamp * 1000,
                  date: TimeString,
                  siloname: Feed.siloname,
                  amount: Math.round(Feed.amount *1000)/1000,
                  feedunit: Feed.funit?Feed.funit:0
                }

                if (Unitindex < 0) {
                  DailyLog.push({...UnitItem})
                } else {
                  DailyLog[Unitindex].amount += Feed.amount
                }

                var Unitindex2 = Combined.findIndex(i => {return i.siloname === Feed.siloname})
                if (Unitindex2 < 0) {
                  Combined.push({...UnitItem})
                } else {
                  Combined[Unitindex2].amount += Feed.amount
                }
              }
            }
          })
        });
        
        DailyLog = DailyLog.reverse()
        setFeedList(DailyLog)
        setCombinedFeed(Combined)
      }
    }
    consysDataCalc(isCancelled)
    return () => {
      // clean up
      isCancelled = true
    };
  },[ConsysFeeds])

  if (activeData && penDatas && typeof penDatas !== 'undefined') {
    var count = "0"
    var batch = "-"
    var age = "-"
    var curve = "-"
    var aweight = "0"
    var agrowth = "0"
    var bmin = undefined
    var bmax = undefined

    if (penDatas.datas){
      if (penDatas.datas.count)count = penDatas.datas.count + ''
      if (penDatas.datas.batch)batch = penDatas.datas.batch
      if (batchDatas) {
        age = Math.floor(((Date.now()-batchDatas.time)/(1000*60*60*24))) + '';
        curve = batchDatas.gcurve;
        bmin = batchDatas.datas.devianceMax +''
        bmax = batchDatas.datas.devianceMax +''
      }
      if (weightCalcs) {
        if (weightCalcs.PenWeight) aweight = Math.round(weightCalcs.PenWeight * 1000) / 1000 +''
        if (weightCalcs.PenGrowth) agrowth = Math.round(weightCalcs.PenGrowth * 1000) / 1000 +''
      }
    }

    return (
      <SafeAreaView style={[Layout.fill, Layout.fullSize, Common.backgroundPrimary]}>
        <ScrollView style={[Gutters.smallVPadding, Gutters.smallHPadding]}>
          <View style={[Layout.fill, Layout.fullSize, Layout.colVCenter]}>
            <Button Texts={[activeData.pen]} Title={true}/>
            <View style={{height:20}}/>
            <ContentProp Columns={2} Datas={[[t('Pen_Display.Prop1'), count], [t('Pen_Display.Prop2'), batch], [t('Pen_Display.Prop3'), age], [t('Pen_Display.Prop4'), aweight],
                                             [t('Pen_Display.Prop5'), agrowth], [t('Pen_Display.Prop6'), curve], [t('Pen_Display.Prop7'), t('Pen_Display.Prop7Data', {min:bmin,max:bmax})], 
                                             [t('Pen_Display.Prop8'), t('Pen_Display.Prop8Time',{date:LastFeed&&LastFeed.timestamp?new Date(LastFeed.timestamp*1000):undefined})], [t('Pen_Display.Prop9'), LastFeed&&LastFeed.amount?LastFeed.amount+'':""]]} />
            <View style={{height:20}}/>
            
            {ConsysFeeds&&ConsysFeeds.length>0&&(
              <View style={[Layout.fullWidth, {marginVertical:15}]}>
                {CombinedFeed&&CombinedFeed.length&&(
                  <View style={[Layout.fullWidth, Layout.colVCenter, {marginBottom:10}]}>
                    <ContentProp Columns={1} Datas={[["Silo log"]]} />
                    <View style={{height:10}} />
                    <FlatList style={[Layout.fullWidth, {maxWidth:600, minHeight:125, maxHeight:250}]}
                      data={CombinedFeed}
                      renderItem={({item, index}) => {
                        var weight = item.amount?(Math.round(item.amount * 1000) / 1000) +'':'-'
                        return(
                          <View style={[Layout.fullSize, Layout.colVCenter]}>
                            <ContentProp Columns={2} Datas={[["Silo: " + item.siloname, weight]]} />
                          </View>
                        )}
                      }
                      //Setting the number of column
                      numColumns={1}
                      keyExtractor={(item, index) => String(index)}
                    />
                  </View>
                )}
                {FeedList&&FeedList.length>0&&(
                  <View style={[Layout.fullWidth, Layout.colVCenter]}>
                    <ContentProp Columns={1} Datas={[["Daily Feed log"]]} />
                    <View style={{height:10}} />
                    <FlatList style={[Layout.fullWidth, {maxWidth:600, minHeight:125, maxHeight:250}]}
                      data={FeedList}
                      renderItem={({item, index}) => {
                        var weight = item.amount?(Math.round(item.amount * 1000) / 1000) +'':'-'
                        return(
                          <View style={[Layout.fullSize, Layout.colVCenter]}>
                            <ContentProp Columns={2} Datas={[[item.date + " : silo: " + item.siloname, weight]]} />
                          </View>
                        )}
                      }
                      //Setting the number of column
                      numColumns={1}
                      keyExtractor={(item, index) => String(index)}
                    />
                  </View>
                )}
              </View>
            )}

            <View style={[Layout.fullWidth, Layout.alignItemsCenter, Common.border, isApp()?{width:'80%'}:{}, {minHeight:400, minWidth:400, marginBottom:20}]}>
              <CurveViewer WeighningsDatas={{Batch:batchDatas, TempCurve:graphDatas, Weights:{[activeData.pen]:weighingsDatas}}} 
                           AutoScale={{Ids:ConsysScales, stop:batchDatas&&batchDatas.time?batchDatas.time:null, Distribution:true}} 
                           Feed={{Ids:ConsysFeeds, stop:batchDatas&&batchDatas.time?batchDatas.time:null}} maxHeight={650} />
            </View>

            <View style={[Layout.fullWidth, Layout.colVCenter, {marginBottom:20}]}>
              <ContentProp Columns={1} Datas={[[t('Pen_Display.ControlWeights_Title')]]} />
              <View style={{height:10}} />
              <FlatList style={[Layout.fullWidth, {maxWidth:600, minHeight:125, maxHeight:250}]}
                data={weighingsDatas?Object.keys(weighingsDatas).filter(e => !weighingsDatas[e].deleted && !weighingsDatas[e].edited).reverse():null}
                renderItem={({item, index}) => {
                  const WeightData = weighingsDatas[item]
                  var weight = Math.round(WeightData.weight * 1000) / 1000 +''
                  return(
                    <View style={[Layout.fullSize, Layout.colVCenter]}>
                      <ContentProp Columns={2} Datas={[[t('Pen_Display.WeightTime',{date:new Date(WeightData.time)}), t('Pen_Display.WeightData', {weight: weight, count: WeightData.count})]]} />
                    </View>
                  )}
                }
                  //Setting the number of column
                  numColumns={1}
                  keyExtractor={(item, index) => String(index)}
                />
            </View>

            <View style={[Layout.fullWidth, Layout.colVCenter, {marginBottom:20}]}>
              <ContentProp Columns={1} Datas={[[t('Pen_Display.ActivityLog_Title')]]} />
              <View style={{height:10}} />
              <FlatList style={[Layout.fullWidth, {maxWidth:600, minHeight:125, maxHeight:250}]}
                data={commentsDatas?Object.keys(commentsDatas).reverse():null}
                renderItem={({item, index}) => {
                  const CommentData = commentsDatas[item]
                  var weight = CommentData.weight?Math.round(CommentData.weight * 1000) / 1000 +'':'-'
                  var DataString = ""
                  if (CommentData.action === Operations.NOTE) DataString = t('Pen_Display.NoteData', {note:CommentData.data})
                  else DataString = t('Pen_Display.ActivityData', {count: CommentData.quantity, action: CommentData.action, weight:weight, note:CommentData.data})
                  return(
                    <View style={[Layout.fullSize, Layout.colVCenter]}>
                      <ContentProp Columns={2} Datas={[[t('Pen_Display.ActivityTime',{date:new Date(CommentData.time)}), DataString]]} />
                    </View>
                  )}
                }
                  //Setting the number of column
                  numColumns={1}
                  keyExtractor={(item, index) => String(index)}
                />
            </View>

            <OperationsPanel Type={3}/>
          </View>
        </ScrollView>
      </SafeAreaView>
    )
  } else {
    return (<LoadingSplash />) 
  }
}

export default Pen_Display
