import {
  buildAsyncState,
  buildAsyncReducers,
  buildAsyncActions,
} from '@thecodingmachine/redux-toolkit-wrapper'
import {fetch, update} from '../../Services/Consys/Feed'
import handleError from '../../Services/utils/handleError'

export default {
  initialState: buildAsyncState(),
  action: buildAsyncActions('consys/feed', fetch),
  //update: buildAsyncActions('consys/feed', update),
  //create: buildAsyncActions('consys/feed', create),
  
  reducers: buildAsyncReducers({
    itemKey: 'feed',
    errorKey: 'error',
    loadingKey: 'loading',
  }),
}